.activities-card-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-block: 0px;
  gap: unit-converter(32, px, rem);

  @media (max-width: 1236px) {
    justify-content: space-evenly;
  }

  @media (max-width: 816px) {
    justify-content: center;
  }
}