.popular-destinations-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: unit-converter(32, px, rem);

  @media (max-width: 816px) {
    justify-content: space-evenly;
  }

  @media (max-width: 629px) {
    justify-content: center;
  }
}
